@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
:root {
    --primary: #000000;
    --secondary: #383838;
    --background: #949494;
    --white: #ffffff;
    --black: #000000;

    --accent: #1f002b;
    
    --primary-text: #ffffff;
    --secondary-text: #ffffff;
    --accent-text: #ffffff;
}
* {
  font-family: 'Pacifico';
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--background);
  background-image: url(/static/media/background.0fd8f04d.png);
}

.Title {
  color: var(--black);

  font-weight: bold;
  /* font-size: 3vw; */
}

.Text {
  color: var(--white);

  line-height: 1;
  font-weight: normal;
  font-size: 3vw;
  max-width: 1000%;
  margin-bottom: 1.5vh;
  text-align: center;
}
.App {
  text-align: center;
}

.AboutImage {
  position: relative;
  display: inline-block;

  width: 100%;
  height: auto;

  -webkit-filter: brightness(50%);

          filter: brightness(50%);
}

.RoadmapImage {
  position: relative;
  display: inline-block;

  width: 80%;
  height: auto;
}

.Wrapper {
  margin-top: 15vh;
  margin-bottom: 15vh;
}

.IntroVideo {
  object-fit: contain;
  width: 80%;
  max-height: 80vh;
}

#story {
  padding-top: 10vh;
}

.normalfont {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 10px;
}

.blacktext {
  color: var(--black)
}

#teamTitle {
  margin-top: 10vh;
}

table {
  width: 80vw;

  margin-left: 10vw;
}

.TeamMember {
  background-color: var(--secondary);
  border-radius: 15px;
}

.TeamMemberImage {
  margin-top: 2vh;
  max-width: 90%;
  max-height: 200px
}

.TeamMemberName {
  color: var(--white);
  margin: 0;
}

.TeamMemberRole {
  color: var(--white);
  margin: 0;
  margin-top: .5vh;
}

@media only screen and (max-width: 768px) {  
  #story {
    margin-top: 2vh;
    margin-bottom: 2vh;
  }
}


.Header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: var(--primary);

  height: 10vh;
  align-items: center;
}

.headerplacement {
  display: inline-block;
}


.navigating {
  float: left;
  display: inline-block;

  padding-right: 1vw;

  text-align: center;
  justify-content: center;
  align-items: center;
}

.text {
  color: var(--white);

  margin-top: 2vh;
  margin-bottom: 2vh;
  margin-left: 2vh;
  margin-right: 2vh;


  height: 6vh;
  line-height: 6vh;

  font-size: 1.5vw;
  text-align: center;
}

.ConnectText {
  background-color: var(--secondary);
  float: right;

  border-radius: 10px;
  margin: 1.5vh;
  padding: .5vh 1vh .5vh 1vh;
}

@media only screen and (max-width: 475px) {
  .navigating {
    width: 90%;
    height: 40%;

    margin-left: 5%;
    margin-top: .5vh;

    float: left;
  }

  .text {
    margin-top: 0vh;
    margin-bottom: 0vh;
    margin-left: .5vh;
    margin-right: .5vh;

    padding-top: 5px;
    padding-bottom: 5px;

    height: 90%; 
    line-height: normal;
 
    font-size: 2vh;
  }
}

@media only screen and (min-width: 475px) and (max-width: 768px) {  
  .text {
    font-size: 3vw;
  }
}
